import {
  LitElement, html, customElement, property, css
} from 'lit-element';

import { classMap } from 'lit-html/directives/class-map';
import ky from 'ky';

import './pill.ts';
import './nft-card-front.ts';
import './nft-card-back.ts';

// import { OpenSeaPort, Network } from 'opensea-js'

interface Images {
    main: string;
    [key:string]: string;
}

/**
 * nft-card element that manages front & back of card.
 * Facilitates aquisition and distribution data between
 * components.
 * Registers <nft-card> as an HTML tag.
 */
@customElement('nft-card')
export class NftCard extends LitElement {

  @property({type : String})  orientation = 'vertical';
  @property({type : String})  flippedCard = false;

  @property({type : Boolean})  loading = true;

  /******* For Testing *******/
  @property({type : Object})  asset = {};
  @property({type : Object})  traitData = {};
  /******* For Testing *******/

  // public loading: boolean;

  constructor() {
    super();
    // this.loading = true;
    // this.seaPort = new OpenSeaPort()
  }


  async getAsset(contractAddress, tokenId) {
    // TODO: Change to live request - handle errors
    // console.log(await this.seaPort.getAsset('0x5caebd3b32e210e85ce3e9d51638b9c445481567', 2242579050293992223))
    return ky.get('./war-rider-nft.json').json();
  }


  /**
   * connectedCallback - Invoked when a component is added to the documents DOM.
   * Grabs data from the OpenSea SDK and populates data objects to be passed to
   * child components.
   */
  async connectedCallback() {
    super.connectedCallback()
    this.asset = await this.getAsset('', '')
    this.traitData = {
      traits: this.asset.traits,
      collectionTraits: this.asset.collection.traits
    }
    // We got the data so we are done loading
    this.loading = false;

    // Tell the component to update with new state
    this.requestUpdate();
  }

  // @TODO: Add dynamic styles based on orientation prop
  static get styles() {
    return css`
        p {
          margin: 0;
          -webkit-font-smoothing: antialiased;
        }
        .card {
          background-color: transparent;
          font-family: Avenir Next, Avenir, Helvetica Neue, sans-serif;
          font-style: normal;
          font-weight: normal;
          border-radius: 5px;
          perspective: 1000px;
       }
       .card-inner {
          position: relative;
          width: 100%;
          height: 100%;
          text-align: center;
          transition: transform 0.6s;
          transform-style: preserve-3d;
          box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.25);
          border-radius: 5px;
          min-height: 160px;
      }
      .flipped-card .card-inner {
        transform: rotateY(180deg);
      }
    `;
  }

  buyEvent(data) {
    console.log(data)
  }

  flipCard(data) {
    this.flippedCard = !this.flippedCard;
  }

  eventHandler(event) {
    const { detail } = event
    switch(detail.type) {
      case 'buy': this.buyEvent(detail.data); break;
      case 'flip': this.flipCard(); break;
    }
  }

  /**
   * Implement `render` to define a template for your element.
   */
  render() {
    /**
     * Use JavaScript expressions to include property values in
     * the element template.
     */
    return html`

      <style>
        @import url('https://fonts.googleapis.com/css?family=Roboto:100,300,400,500&display=swap');
      </style>
      <div class="card ${this.flippedCard ? 'flipped-card' : ''}">
        <div class="card-inner">
          ${this.loading ? html`loading` : html`
            <nft-card-front @new-event="${this.eventHandler}" .asset=${this.asset} ></nft-card-front>
            <nft-card-back .traitData=${this.traitData} ></nft-card-back>`
          }
        </div>
      </div>
    `;
  }
}
